.main {
	background-color: #f5f5f5;
	height: 100%; }
.header {
	padding-top: 40px;
	width: 100%;
	@include clr; }
.logo {
	width: 120px;
	height: 50px;
	float: left; }
.nav {
	float: right;
	@include r(550) {
		width: 100%; } }
.nav__item {
	display: inline-block;
	line-height: 50px;
	text-align: center;
	margin-left: 30px;
	@include r(550) {
		display: none;
		margin-left: 0; }
	@include r(720) {
		margin-left: 10px; } }
.header__lang {
	width: 75px;
	height: 35px;
	border: 1px solid #dfdfe2;
	border-radius: 15px;
	float: right;
	margin-left: 120px;
	cursor: pointer;
	position: relative;
	top: 50%;
	transform: translateY(25%);
	font-family: "GothamProBlack";
	color: $main_color;
	font-size: 11px;
	line-height: 35px;
	@include r(720) {
		margin-left: 20px; }
	@include r(550) {
		display: none; }
	span {
		width: 20px;
		height: 20px;
		border-radius: 50%;
		background-color: #dfdfe2;
		display: inline-block;
		margin: 7px 18px 10px 5px;
		float: left; } }
.nav__item__link {
	text-decoration: none;
	text-transform: uppercase;
	font-family: "GothamProMedium";
	color: #3f4147;
	font-size: 12px;
	&:hover {
		color: $main_color; } }
.main__right {
	margin-top: 100px;
	width: 45%;
	float: left;
	@include r(640) {
		width: 100%;
		text-align: center; } }
.main__right__caption {
	font-family: "GothamProBold";
	font-size: 30px;
	line-height: 35px;
	color: #3f4147;
	text-align: left;
	width: 60%;
	@include r(640) {
		width: 100%;
		text-align: center; } }
.main__right_line {
	margin-top: 35px;
	margin-bottom: 35px; }
.main__right__data {
	font-family: "GothamProRegular";
	color: #414252;
	font-size: 15px;
	line-height: 24px;
	margin-bottom: 40px;
	span {
		font-family: "GothamProBold"; } }
.order {
	width: 180px;
	height: 50px;
	border-radius: 25px;
	background-color: $main_color;
	color: #fff;
	text-transform: uppercase;
	font-family: "GothamProBlack";
	font-size: 11px;
	line-height: 30px;
	margin-bottom: 60px; }
.main__play {
	width: 10%;
	float: left;
	height: 1px;
	@include r(640) {
		display: none; } }
.main__play__round {
	display: block;
	margin: 0 auto;
	width: 90px;
	height: 90px;
	border-radius: 50%;
	background-color: #fff;
	position: absolute;
	top: 50%;
	transform: translateY(-90%); }
.main__right__item {
	width: 85%;
	height: 100%;
	@include r(960) {
		width: 100%; } }
.main__right_top {
	margin-top: 0px;
	height: 100%;
	@include r(640) {
		display: none; } }
.caption-wrap {
	width: 50%;
	margin: 0 auto;
	text-align: center;
	padding: 60px 0 60px 0; }
.line_center {
	margin: 20px auto; }
.caption {
	font-family: "GothamProBold";
	font-size: 30px;
	color: $dark_grey;
	text-transform: uppercase; }
.caption-wrap__data {
	font-family: "GothamProRegular";
	font-size: 15px;
	color: $light_grey;
	span {
		font-family: "GothamProBold"; } }
.services__wrap {
	padding-top: 55px;
	height: 100%;
	padding-bottom: 30px;
	border-top: 1px solid lightgrey;
	border-bottom: 2px dotted lightgrey; }
.services__wrap__item {
	width: 40%;
	height: 100%;
	&_left {
		float: left; }
	@include r(640) {
		width: 100%;
		margin-bottom: 50px; } }
.sprite {
	width: 40px;
	float: left;
	margin-right: 30px;
	@include r(1220) {
		margin-right: 3%; }
	@include r(640) {
		margin-left: 5%; } }
.services-list {
	float: left;
	width: 75%;
	@include r(640) {
		width: 80%; } }
.service-list__caption {
	margin-top: 5px;
	width: 65%;
	margin-bottom: 30px;
	font-family: "GothamProBold";
	font-size: 15px;
	text-transform: uppercase; }
.services-list__text {
	font-family: "GothamProRegular";
	font-size: 13px;
	color: $light_grey;
	line-height: 20px; }
.services-list__main {
	font-family: "GothamProRegular";
	font-size: 13px;
	color: $light_grey;
	line-height: 20px;
	list-style-image: url('/img/circle.png');
	margin-left: 20px; }
.info {
	width: 30px;
	height: 30px;
	float: right;
	border-radius: 50%;
	border: 2px solid $main_color;
	text-align: center;
	font-family: "BanderaPro-Bold";
	font-size: 18px;
	line-height: 28px;
	transition: .2s ease all;
	@include r(940) {
		display: none; } }
.services__wrap__item_right {
	float: right; }
.services__wrap_border {
	border-top: 0;
	border-bottom: 1px solid lightgrey; }
.service-list__caption_big {
	width: 100%; }
.reviews-wrap {
	height: 100%;
	@include clr; }
.reviews__item {
	width: 33%;
	padding-left: 3%;
	float: left;
	@include clr;
	@include r(640) {
		width: 100%; } }
.reviews__item__media-icon {
	margin-bottom: 25px; }
.reviews__item__quote {
	font-family: "GothamProRegular";
	font-size: 17px;
	line-height: 25px;
	color: $light_grey; }
.rewiews__item__person {
	margin-top: 40px; }
.person__photo {
	width: 50px;
	height: 50px;
	border-radius: 50%;
	cursor: pointer;
	@include r(640) {
		margin: 0 auto;
		display: block; } }
.person__name {
	margin: 20px 0 20px 0;
	font-family: "GothamProBold";
	font-size: 13px;
	line-height: 20px;
	cursor: pointer;
	@include r(640) {
		margin: 10px auto;
		display: block;
		width: 100%;
		text-align: center; } }
.person__dept {
	font-family: "GothamProRegular";
	font-size: 12px;
	float: left;
	cursor: pointer;
	@include r(640) {
		float: none; } }
.person__from {
	@include clr;
	@include r(640) {
		margin: 0 auto;
		display: block;
		width: 150px;
		text-align: center; } }
.dote {
	float: left;
	margin: 0 5px 0 5px;
	font-size: 30px;
	position: relative;
	top: -18px;
	color: $main_color;
	@include r(640) {
		display: none; } }
.person__site {
	text-decoration: none;
	font-family: "GothamProBold";
	font-size: 12px;
	color: #414252;
	float: left;
	@include r(640) {
		float: none; } }
.media__video {
	width: 80%;
	height: 165px;
	cursor: pointer;
	@include r(640) {
		width: 50%;
		margin: 0 auto;
		display: block; } }
.download {
	width: 100%;
	height: 100%;
	margin-top: 50px;
	margin-bottom: 50px; }
.download__button {
	margin: 0 auto;
	display: block;
	background-image: none;
	background-color: transparent;
	font-family: "GothamProBlack";
	font-size: 11px;
	text-transform: uppercase; }
.request {
	border-bottom: 1px solid lightgrey; }
.form-left {
	width: 48%;
	float: left;
	@include clr;
	@include r(640) {
		width: 100%; } }
.input {
	display: block;
	width: 100%;
	border-bottom: 1px solid lightgrey;
	margin-bottom: 25px;
	height: 30px;
	font-family: "GothamProBold";
	text-transform: uppercase;
	font-size: 11px; }
.interest {
	width: 100%;
	height: 100%;
	margin-top: 50px;
	border:	1px solid lightgrey; }
.interest__header {
	width: 100%;
	height: 50px;
	padding: 20px;
	background-color: #fcfcfd;
	border-bottom: 1px solid lightgrey; }
.interest__header__caption {
	font-family: "GothamProMedium";
	font-size: 11px;
	text-transform: uppercase;
	color: $light_grey;
	float: left; }
.interest__list {
	list-style-image: url('/img/circle_grey.png'); }
.interest__list__item {
	height: 40px;
	padding-left: 20px;
	font-family: "GothamProMedium";
	font-size: 13px;
	color: grey;
	cursor: pointer;
	&:hover {
		list-style-image: url('/img/circle.png');
		font-family: "GothamProBold";
		color: $main_color; } }
.interest__data {
	padding: 30px 40px 0 40px; }
.form-right {
	width: 47%;
	float: right;
	@include clr;
	@include r(640) {
		width: 100%;
		margin-top: 30px; } }
.form-textarea {
	display: block;
	margin-top: 20px;
	width: 100%;
	height: 250px;
	padding: 20px;
	border: 1px solid lightgrey;
	font-family: "GothamProRegular";
	font-size: 15px; }
.order_center {
	display: block;
	margin: 30px auto; }
.footer {
	text-align: center; }
.footer__caption {
	margin: 30px 0;
	text-transform: uppercase;
	font-family: "GothamProBlack";
	font-size: 11px; }
.footer__contact {
	font-family: "GothamProRegular";
	font-size: 18px;
	line-height: 30px;
	margin-bottom: 15px; }
.footer__email {
	font-family: "GothamProRegular";
	font-size: 18px;
	color: $main_color; }
.copyrights {
	font-family: "GothamProRegular";
	font-size: 12px;
	line-height: 16px;
	margin-bottom: 30px;
	span {
		font-family: "GothamProBold"; } }
