@include font(GothamProMedium, GothamProMedium);
@include font(GothamProBold, GothamProBold);
@include font(GothamProRegular, GothamProRegular);
@include font(GothamProBlack, GothamProBlack);
@include font(BanderaPro-Bold, BanderaPro-Bold);

$main_color: #a70c5d;
$dark_grey: #3f4147;
$light_grey: #414252;

body {
	box-sizing: border-box; }
.center {
	width: 86%;
	max-width: 1240px;
	margin: 0 auto;
	@include clr;
	@include r(550) {
		width: 100%; } }
.center_big {
	width: 100%;
	max-width: 1400px;
	@include clr; }
.line {
	width: 40px;
	height: 2px;
	background-color: $main_color;
	display: block;
	@include r(640) {
		width: 100%; } }
.container {
	height: 100%;
	position: relative;
	@include clr; }
.menu {
	width: 50px;
	height: 50px;
	border: 1px solid $main_color;
	float: right;
	border-radius: 10px;
	display: none;
	cursor: pointer;
	transition: 0.5s ease all;
	@include r(550) {
		display: inline-block; }
	span {
		width: 80%;
		display: block;
		height: 2px;
		transition: .5s ease all;
		background-color: $main_color;
		margin: 10px auto; } }
.menu.active {
	span:nth-child(1) {
		transform: rotate(45deg);
		position: relative;
		top: 12px; }
	span:nth-child(2) {
		transform: rotate(-45deg); }
	span:nth-child(3) {
		transform: rotate(180deg);
		opacity: 0; } }
.active_nav {
	width: 100%;
	z-index: 100;
	position: absolute;
	top: 90px;
	.nav__item {
		display: block;
		width: 100%;
		height: 100%;
		background-color: grey;
		text-align: center;
		&:hover {
			background-color: $light_grey;
			a {
				color: #fff; } } } }
.header_active {
	background-color: grey; }
.icon {
	transition: .2s ease all; }
.icon-document {
  width: 40px;
  height: 40px;
  fill: #b2b2b2; }
.icon-target {
	width: 40px;
	height: 40px;
	fill: #b2b2b2; }
.icon-note {
 	width: 40px;
  height: 40px;
  fill: #b2b2b2; }
.icon-sud {
 	width: 40px;
  height: 40px;
  fill: #b2b2b2; }
.icon-play {
	width: 1.1em;
	height: 1.3em;
	fill: $main_color; }
.icon-order {
	width: 2em;
	height: 2em;
	position: relative;
	top: 5px;
	left: -10px; }
.icon-left-quote {
	width: 20px;
	height: 20px;
	fill: #c9cacf; }
.icon-youtube {
	width: 30px;
	height: 25px;
	fill: #c9cacf; }
.icon-download {
	width: 20px;
	height: 20px;
	fill: $main_color;
	margin-right: 10px;
	position: relative;
	top: 5px; }
.icon-form {
	float: right;
	width: 20px;
	height: 20px;
	fill: grey;
	margin-top: -5px; }
.icon-facebook {
	width: 40px;
	height: 40px;
	fill: lightgrey;
	margin: 25px 0;
	cursor: pointer;
	&:hover {
		fill: blue; } }
.icon-twitter {
	width: 40px;
	height: 40px;
	fill: lightgrey;
	margin: 25px 0;
	cursor: pointer;
	&:hover {
		fill: blue; } }
.icon-in {
	width: 40px;
	height: 40px;
	fill: lightgrey;
	margin: 25px 0;
	cursor: pointer;
	&:hover {
		fill: blue; } }
